import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { DirectGrnHoRoutingModule } from './direct-grn-ho-routing.module';
import { DirectGrnHoComponent } from './direct-grn-ho.component';
import { DirectGrnHoAddComponent } from './direct-grn-ho-add/direct-grn-ho-add.component';
import { DirectGrnHoEditComponent } from './direct-grn-ho-edit/direct-grn-ho-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    DirectGrnHoRoutingModule
  ],
  declarations: [
    DirectGrnHoComponent, 
    DirectGrnHoAddComponent, 
    DirectGrnHoEditComponent
  ],
  entryComponents: [
    DirectGrnHoAddComponent, 
    DirectGrnHoEditComponent
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class DirectGrnHoModule { }



