  constructor(
    private fb: FormBuilder,
    @Inject('API_URL') apiUrl: string,
    private prdms: PoRequisitionDetailMasterService,
    private gps: GrnPoMasterService,
    private itemService: ItemMasterService,
    private cdr: ChangeDetectorRef,
    private ccs: CodeCheckerService,
    private ehs: ErrorHandleService,
    private cus: CurrentUserService
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.createForm();
  }

  ngOnInit() {
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
          { name: 'code' },
        ],
        url: this.apiUrl + 'store?limit=all',
        data: this.userData
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });

    this.vendor_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'vendor?limit=all',
      };
    this.vendorAdapter = new jqx.dataAdapter(this.vendor_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });

    this.miSource =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'issue_no' },
        ],
        data: this.userData,
        url: this.apiUrl + 'material_issue?limit=all',
      };

    this.miAdapter = new jqx.dataAdapter(this.miSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });

    this.grnpoitemSource =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'po_no' }
        ],
        data: this.userData,
        url: this.apiUrl + 'po_requisition_detail?limit=all'
      };
    this.grnpoitemAdapter = new jqx.dataAdapter(this.grnpoitemSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });

    this.costCenterSource =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        async: false,
        data: this.userData,
        url: this.apiUrl + 'costcenter?limit=all'
      };

    this.costCenterAdapter = new jqx.dataAdapter(this.costCenterSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        this.achLoader = false;
        return tData;
      },
      loadError: (jqXHR, status, error) => {
        this.achLoader = false;
      }
    });

    this.accHeadItemSource = {
      datatype: "json",
      contentType: "application/json; charset=utf-8",
      datafields: [
        { name: 'id' },
        { name: 'name' },
        { name: 'code' }
      ],
      data: this.userData,
      url: this.apiUrl + 'account_head?limit=all'
    };

    this.accHeadItemAdapter = new jqx.dataAdapter(this.accHeadItemSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        for (let i = 0; i < tData.length; i++) {
          tData[i]['code'] = tData[i]['code'] + ' - ' + tData[i]['name'];
        }
        this.achLoader = false;
        return tData;
      },
      loadError: (jqXHR, status, error) => {
        this.achLoader = false;
      }
    });

    // For items in Item Add Grid
    this.itemSource =
      {
        datatype: 'json',
        datafields:
        [
          { name: 'id', type: 'number' },
          { name: 'name', type: 'string' },
          { name: 'code', type: 'string' },
          // { name: 'po_id', type: 'string' },
          // { name: 'item_id', type: 'string', map: 'name' },
          // { name: 'quantity', type: 'number' },
          // { name: 'unit_id', type: 'number' },
          // { name: 'unit_name', type: 'string' },
          // { name: 'remarks', type: 'string' },
          // { name: 'item_id', type: 'string' },
          // { name: 'item_name', type: 'string' },
          // { name: 'quantity', type: 'string' },
          // { name: 'rate', type: 'string' },
          // { name: 'unit_id', type: 'string' },
          // { name: 'unit_name', type: 'string' },
          // { name: 'value', type: 'string' },
          // { name: 'vat', type: 'string' },
          // { name: 'total', type: 'string' },
        ],
        updaterow: (rowid: any, rowdata: any, commit: any): void => {
          commit(true);
        },
        data: this.userData,
        url: this.apiUrl + 'item?limit=all'
      };

    this.itemAdapter = new jqx.dataAdapter(this.itemSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        for (let i = 0; i < tData.length; i++) {
          tData[i]['name'] = tData[i]['code'] + ' - ' + tData[i]['name'];
        }
        this.itemLoader = false;
        return tData;
      },
    });

  }

  ngAfterViewInit() {
    this.datetime.value(new Date());
    let codeData = {
      find_is_no:'grn_po',
      return_type: 'for_client'
    }
    this.ccs.get(codeData).subscribe(
      response => {
        this.grnPoForm.controls['grn_no'].setValue(response['grn_no']);
      },
      error => {
        console.log(error);
      }
    );
    this.cdr.detectChanges();
  }

  createForm() {
    this.grnPoForm = this.fb.group({
      'grn_no': [null, Validators.required],
      'grn_date': [null, Validators.required],
      'vendor_chalaan_no': [null],
      'bill_code': [null, Validators.required],
      'bill_no': [null, Validators.required],
      'remarks': [null],
    });
  }

  /**
   * Track Change Event of Type Combobox
   * @param  
   */
  typeChange($event) {
    let selectedType = this.directGrnType.getSelectedItem().value;
    switch (selectedType) {
      case 'sup': {
        this.vendorShow = true;
        this.miShow = false;
        break;
      }
      case 'adj': {
        this.vendorShow = false;
        this.miShow = false;
        break;
      }
      case 'ret': {
        this.vendorShow = false;
        this.miShow = true;
        break;
      }
      case 'sis': {
        this.vendorShow = false;
        this.miShow = true;
        break;
      }
      default: {
        break;
      }
    }
    // switch
  }

