  /**
   * Add New Row in Grn Po Grid
   */
  addNewItem() {
    let datarow = this.generaterow();
    this.grnPoAddGrid.addrow(null, datarow);
  }

  /**
   * Initialize Row data field to be pushed to Grn Po Grid
   */
  generaterow(): any {
    let row = {};
    row['item_id'] = '';
    row['quantity'] = '';
    row['rate'] = '';
    row['unit_id'] = '';
    row['value'] = '';
    row['vat'] = '13';
    row['vat_value'] = '';
    row['total'] = '';
    return row;
  }

  //  Start of account Head Grid
  achLoader: boolean = false;

  /**
 * Add New Row in Account Head Grid
 */
  addAccountHeadItem() {
    let datarow = this.generateAccountHeadRow();
    this.accHeadAddGrid.addrow(null, datarow);
  }

  /**
 * Initialize Row data field to be pushed to Account Head Grid
 */
  generateAccountHeadRow(): any {
    let row = {};
    row['account_head'] = '';
    row['cr_dr'] = '';
    row['amount'] = '';
    row['cost_center'] = '';
    row['total'] = '';
    return row;
  }

