import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DirectGrnHoComponent } from './direct-grn-ho.component';

const routes: Routes = [
    {
        path: '', component: DirectGrnHoComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DirectGrnHoRoutingModule { }



